<script>
  import { data } from './stores.js';

	let selected = {
		code: null,
		name: null
	};

	let options = [];
	for (const i in $data) {
		let option = {};
		option.code = i;
		option.name = $data[i].name;
		options.push(option);
	}
	
	$: place = $data[selected.code];

	let total = options.length;
	
	// function to add leading plus to positive numbers
	function pm(val) {
		if (val > 0) {
			return "+" + val;
		} else {
			return val;
		}
	}
	
</script>

<style>
	.muted {
		color: #999;
	}
</style>

<p>
	<select bind:value={selected}>
		<option>
			Select a place
		</option>
		{#each options as option}
			<option value={option}>
				{option.name}
			</option>
		{/each}
	</select>
</p>

{#if selected.code != null}
<h1>{place.name}</h1>
<ul class="list">
  <li>Area: <strong>{place.area.toLocaleString()} hectares</strong></li>
	<li>Population: <strong>{place.data.population.val.c2011.all.toLocaleString()}</strong> ({pm(place.data.population.val.change.all)}%) <small class="muted">{place.data.population.rank.change.all} of {total}</small></li>
	<li>Density: <strong>{place.data.density.val.c2011.density.toLocaleString()} people per hectare</strong> ({pm(place.data.density.val.change.density)}%) <small class="muted">{place.data.density.rank.change.density} of  {total}</small></li>
  <li>Median age: <strong>{place.data.medage.val.c2011.median}</strong> ({pm(place.data.medage.val.change.median)}%) <small class="muted">{place.data.medage.rank.change.median} of {total}</small></li>
  <li>Age groups:
		<ul>
			<li>0-15: <strong>{place.data.age.perc.c2011.a015}%</strong> ({pm(place.data.age.perc.change.a015)}%) <small class="muted">{place.data.age.rank.change.a015} of {total}</small></li>
			<li>16-64: <strong>{place.data.age.perc.c2011.a1664}%</strong> ({pm(place.data.age.perc.change.a1664)}%) <small class="muted">{place.data.age.rank.change.a1664} of {total}</small></li>
			<li>65+: <strong>{place.data.age.perc.c2011.a65plus}%</strong> ({pm(place.data.age.perc.change.a65plus)}%) <small class="muted">{place.data.age.rank.change.a65plus} of {total}</small></li>
	  </ul>
	</li>
	<li>Ethnicity:
		<ul>
			<li>White: <strong>{place.data.ethnicity.perc.c2011.white}%</strong> ({pm(place.data.ethnicity.perc.change.white)}%) <small class="muted">{place.data.ethnicity.rank.change.white} of {total}</small></li>
			<li>Asian: <strong>{place.data.ethnicity.perc.c2011.asian}%</strong> ({pm(place.data.ethnicity.perc.change.asian)}%) <small class="muted">{place.data.ethnicity.rank.change.asian} of {total}</small></li>
			<li>Black: <strong>{place.data.ethnicity.perc.c2011.black}%</strong> ({pm(place.data.ethnicity.perc.change.black)}%) <small class="muted">{place.data.ethnicity.rank.change.black} of {total}</small></li>
			<li>Mixed: <strong>{place.data.ethnicity.perc.c2011.mixed}%</strong> ({pm(place.data.ethnicity.perc.change.mixed)}%) <small class="muted">{place.data.ethnicity.rank.change.mixed} of {total}</small></li>
			<li>Other: <strong>{place.data.ethnicity.perc.c2011.other}%</strong> ({pm(place.data.ethnicity.perc.change.other)}%) <small class="muted">{place.data.ethnicity.rank.change.other} of {total}</small></li>
	  </ul>
	</li>
	<li>Housing tenure:
		<ul>
			<li>Owned: <strong>{place.data.tenure.perc.c2011.owned}%</strong> ({pm(place.data.tenure.perc.change.owned)}%) <small class="muted">{place.data.tenure.rank.change.owned} of {total}</small></li>
			<li>Shared ownership: <strong>{place.data.tenure.perc.c2011.sharedown}%</strong> ({pm(place.data.tenure.perc.change.sharedown)}%) <small class="muted">{place.data.tenure.rank.change.sharedown} of {total}</small></li>
			<li>Rented (social): <strong>{place.data.tenure.perc.c2011.rentsocial}%</strong> ({pm(place.data.tenure.perc.change.rentsocial)}%) <small class="muted">{place.data.tenure.rank.change.rentsocial} of {total}</small></li>
			<li>Rented (private): <strong>{place.data.tenure.perc.c2011.rentprivate}%</strong> ({pm(place.data.tenure.perc.change.rentprivate)}%) <small class="muted">{place.data.tenure.rank.change.rentprivate} of {total}</small></li>
			<li>Other: <strong>{place.data.tenure.perc.c2011.other}%</strong> ({pm(place.data.tenure.perc.change.other)}%) <small class="muted">{place.data.tenure.rank.change.other} of {total}</small></li>
	  </ul>
	</li>
	<li>Economic activity:
		<ul>
			<li>Employed: <strong>{place.data.economic.perc.c2011.employed}%</strong> ({pm(place.data.economic.perc.change.employed)}%) <small class="muted">{place.data.economic.rank.change.employed} of {total}</small></li>
			<li>Unemployed: <strong>{place.data.economic.perc.c2011.unemployed}%</strong> ({pm(place.data.economic.perc.change.unemployed)}%) <small class="muted">{place.data.economic.rank.change.unemployed} of {total}</small></li>
			<li>Student: <strong>{place.data.economic.perc.c2011.student}%</strong> ({pm(place.data.economic.perc.change.student)}%) <small class="muted">{place.data.economic.rank.change.student} of {total}</small></li>
			<li>Carer: <strong>{place.data.economic.perc.c2011.carer}%</strong> ({pm(place.data.economic.perc.change.carer)}%) <small class="muted">{place.data.economic.rank.change.carer} of {total}</small></li>
			<li>Retired: <strong>{place.data.economic.perc.c2011.retired}%</strong> ({pm(place.data.economic.perc.change.retired)}%) <small class="muted">{place.data.economic.rank.change.retired} of {total}</small></li>
			<li>Inactive: <strong>{place.data.economic.perc.c2011.inactive}%</strong> ({pm(place.data.economic.perc.change.inactive)}%) <small class="muted">{place.data.economic.rank.change.inactive} of {total}</small></li>
	  </ul>
	</li>
</ul>
{/if}